import Vue from 'vue'
(function(owner) {
    /**
     * 
     * @param  {String} key 
     * @return {String}     
     */
    owner.getRoute = function(key) {
        return this.$route.params[key];
    };
    owner.getQuery = function(key) {
        return this.$route.query[key];
    };

    /**
     * $store
     * @param  {key} key 
     * @return {all}     
     */
    owner.getStore = function(key) {
        return this.$store.state[key] || false;
    };

    /**
     * $store
     * @param  {Object} obj keyvalue
     * @return {null}     
     */
    owner.setStore = function(key, value) {
        var obj = {};
        if(typeof(key) === 'string') {
            obj[key] = value;
        } else if (typeof(key) === 'object') {
            obj = key;
        }
        this.$store.commit('setState', obj)
    };

    /**
     * 
     * @return {null} 
     */
    owner.goto = function() {
        this.$router.push.apply(this.$router, arguments);
    };

    /**
     * 
     * @param  {String} e 
     * @return {Boolean}   
     */
    owner.inAttr = function(e) {
        return inAttr(e);
    };

    /**
     * 
     * @param  {String} e 
     * @return {String}   
     */
    owner.timeToDate = function(e) {
        return timeToDate(e);
    };

})(Vue.prototype)



